export default "import { Component, ViewChild } from '@angular/core';\nimport { TabsetComponent } from 'bao-tran-angular/tabs';\n\n@Component({\n  selector: 'demo-tabs-manual-selection',\n  templateUrl: './manual-selection.html'\n})\nexport class DemoTabsManualSelectionComponent {\n  @ViewChild('staticTabs', { static: false }) staticTabs: TabsetComponent;\n\n  selectTab(tabId: number) {\n    this.staticTabs.tabs[tabId].active = true;\n  }\n}\n";